/* Home.css */
body {
    margin: 0;
    font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
    line-height: 1.6;
}

.homeContainer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 20px;
}

.content {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    max-width: 1200px;
    flex-wrap: wrap;
    width: 100%;
}

.textContent {
    flex: 0.6;
    padding-right: 20px;
    min-width: 300px;
    box-sizing: border-box;
}

.profilePicContainer {
    flex: 0.4;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    padding: 20px;
    box-sizing: border-box;
}

.profilePic {
    width: 100%;
    max-width: 335px;
    height: auto;
    border-radius: 8px;
    margin-top: 50px;
    margin-bottom: 20px;
}

.heading {
    font-size: 30px;
    font-weight: normal;
    color: #333;
}

.text {
    color: #666;
    font-size: 16px;
    line-height: 1.6;
}

.cvLink {
    margin-bottom: 20px;
}

.subHeading {
    font-size: 20px;
    font-weight: normal;
    font-style: italic;
    color: #333;
}

.list {
    list-style-type: circle;
    color: #666;
    font-size: 16px;
    line-height: 1.6;
    padding-left: 20px;
}

.sectionContainer {
    width: 100%;
    max-width: 800px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-bottom: 20px;
}

.publication {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-bottom: 3px;
    padding-bottom: 10px;
    border-bottom: 1px solid #eee;
    max-width: 800px;
}

.title::before {
    content: '•';
    margin-right: 10px;
    color: #333;
}

.news::before {
    content: '•';
    margin-right: 10px;
    color: #333;
}

.title {
    font-size: 20px;
    font-weight: normal;
    font-style: italic;
    color: #333;
}

.titleLink {
    text-decoration: none;
    color: #333;
}

.authors {
    font-size: 16px;
    color: #666;
    margin-left: 15px;
    margin-top: 1px;
}

.boldAuthor {
    font-weight: bold;
    color: black;
}

.authorLink {
    color: #0066cc;
    text-decoration: none;
}

.year {
    font-size: 16px;
    color: #666;
    font-style: italic;
    margin-left: 15px;
    margin-top: 1px;
}

.links {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    margin-top: 1px;
    font-size: 15px;
    font-style: italic;
    margin-left: 15px;
}

.news {
    font-size: 18px;
    font-weight: normal;
    font-style: normal;
    color: black;
}

.newsText {
    text-indent: -20px;
    padding-left: 20px;
}

/* Media queries for responsiveness */
@media (max-width: 768px) {
    .content {
        flex-direction: row; /* Keep the row direction on smaller screens */
        justify-content: center; /* Center the content */
        flex-wrap: wrap; /* Allow wrapping */
    }
    .textContent, .profilePicContainer {
        flex: 0.5; /* Split space equally */
        min-width: 50%; /* Ensure both take up equal width */
    }
    .profilePic {
        width: 90%; /* Adjust image width */
        margin-top: 20px;
    }
    .heading {
        font-size: 24px;
    }
    .text {
        font-size: 14px;
    }
    .subHeading {
        font-size: 18px;
    }
    .list {
        font-size: 14px;
    }
}

@media (max-width: 480px) {
    .content {
        flex-direction: column; /* Stack vertically on very small screens */
        align-items: center;
    }
    .textContent, .profilePicContainer {
        flex: 1; /* Full width */
        min-width: 100%; /* Ensure full width */
    }
    .profilePic {
        width: 80%;
        max-width: 200px;
        margin-top: 20px;
    }
    .heading {
        font-size: 20px;
    }
    .text {
        font-size: 12px;
    }
    .subHeading {
        font-size: 16px;
    }
    .list {
        font-size: 12px;
    }
}
